




















import { ITab } from '@/lib/types'
import { ITabProps } from '@/lib/types/components/tabs'
import Vue from 'vue'

export default Vue.extend({
  name: 'tabs-default',
  props: {
    tabs: {
      type: Array as () => ITab[],
      required: true
    },
    tabProps: {
      type: Object as () => ITabProps
    },
    className: {
      type: String
    }
  },
  methods: {
    handleInputChange(tab: any) {
      this.$emit('tab-selected', tab)
    }
  }
})
